/**
 * Canary Logo Component
 *
 * Pre-configured logo component with embedded image.
 * No need for teammates to manage logo assets.
 */

export interface CanaryLogoProps {
  width?: number | string;
  height?: number | string;
  className?: string;
}

export default function CanaryLogo({
  width = "140px",
  height = "24px",
  className = ""
}: CanaryLogoProps) {
  // Logo embedded as base64 PNG (300x53px, 8.6KB)
  const logoDataUrl = "data:image/png;base64,";

  return (
    <img
      src={logoDataUrl}
      alt="Canary"
      width={width}
      height={height}
      className={className}
      style={{
        objectFit: "contain",
      }}
    />
  );
}
